import { TestFile } from "../../types"
import TestList from "./TestList"

interface TestModalProps {
  testFile: TestFile
  showModal: boolean
  handleClose: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void 
}

const TestModal = ({testFile, showModal, handleClose}: TestModalProps) => {
  return (
    <div id="test-modal-wrapper" style={{display: showModal ? "inline-flex" : "none"}}>
      <div id="test-modal" className="test-modal">
        <h2 className="test-modal-title">
          <span className="modal-title">{testFile.filePath}</span>
          {" " /* adding a space between the test name and test result */}
          {testFile!.result !== "" && <span className="modal-result">[{testFile!.result}]</span>}
        </h2>
        <TestList tests={testFile.tests} />
        <button id="close-modal-button" className="close-modal-button" onClick={handleClose}>Close</button>
      </div>
    </div>
  )
}

export default TestModal