import React, {useState} from "react";
import ScreenReaderButtons from "./screenreader-interaction/screenreader-buttons";
import ColorDropdown from "./color-interaction/color-dropdown";
//@ts-ignore
import {AwesomeButton} from "react-awesome-button";
import AboutModal from "./AboutModal";
import {TestFile} from "../../types";

interface AccessibilityBarProps {
    colorPalette: string
    setColorPalette: React.Dispatch<React.SetStateAction<string>>
    testModalIsOpen: boolean
    testFiles: Array<TestFile>
}

function AccessibilityBar({ colorPalette, setColorPalette, testModalIsOpen, testFiles}: AccessibilityBarProps){
    const [aboutModalOpen, setAboutModalOpen] = useState(false);
    function openAboutModal() {
        setAboutModalOpen(true);
    }
    function closeAboutModal() {
        setAboutModalOpen(false);
    }

    return (
        <header id="accessibilityBar" className={colorPalette}>
            <div id="accessibilityBar">
                <AboutModal aboutModalOpen={aboutModalOpen}
                            closeModal={closeAboutModal}
                            colorPalate={colorPalette}/>
                <nav id='visibleMenu'>
                    <ScreenReaderButtons aboutModalOpen={aboutModalOpen}
                                         testModalOpen={testModalIsOpen}
                                         testFiles={testFiles}
                    />
                    <ColorDropdown colorPalette={colorPalette}
                                   setColorPalette={setColorPalette}/>
                    <div id="hideButton">
                        <AwesomeButton type='secondary'
                                       onPress={() => openAboutModal()}>About</AwesomeButton>
                    </div>
                </nav>
            </div>
        </header>
    )
}

export default AccessibilityBar;