import React from "react";

interface ColorDropdownProps {
    colorPalette: string
    setColorPalette: React.Dispatch<React.SetStateAction<string>>
}

function ColorDropdown({ colorPalette, setColorPalette } : ColorDropdownProps) {
        const colorSelectHandler = (event : any) => {
                setColorPalette(event.target.value);
        }
        return(
            <div>
                <h3 id="colorText">Color Adjustment</h3>
                <select id="selectionMenu" onChange={colorSelectHandler}>
                    <option value="normalDark">Normal (Dark)</option>
                    <option value="normalLight">Normal (Light)</option>
                    <option value="colorblindDark">Partial Colorblind-Friendly (Dark)</option>
                    <option value="colorblindLight">Partial Colorblind-Friendly (Light)</option>
                    <option value="grayscaleDark">Grayscale (Dark)</option>
                    <option value="grayscaleLight">Grayscale (Light)</option>
                </select>
                <ul id="exampleColors">
                    <li id="colorItem">
                        <b>Pass Color:</b>
                        <div id="passColor"></div>
                    </li>
                    <li id="colorItem">
                        <b>Fail Color:</b>
                        <div id="failColor"></div>
                    </li>
                </ul>
            </div>
        )
}
export default ColorDropdown;