interface HelpMenuProps {
  showMenu: boolean
  handleClose: () => void
}

const HelpMenu = ({showMenu, handleClose}: HelpMenuProps) => {
  return (
    <div id="help-menu-wrapper" style={{display: showMenu ? "inline-flex" : "none"}}>
      <div id="help-menu">
        <div id="help-menu-text">

          <h2 id="help-menu-title">CS32 System Tester Help</h2>
          <p>
            Welcome to the CS32S System tester! If you want to see how to use the
            scree reader for this site, press the "about" button a the top right
            of the screen.
          </p>
          <h3>Test Structure</h3>
          <p>
            Tests are structured into <code>.test</code> files. Each <code>.test</code> file
            can contain zero or more tests.
          </p>
          <h3>User Inputs</h3>
          <p>
            When you first click open the system tester, you will see a text field,
            and three buttons, below is a description of each of these interactive
            components.
          </p>
          <ul>
            <li>
              <h4>The directory input</h4>
              <p>
                This text input field is where you input the directory that
                contains the <code>.test</code> files that you want to run.
              </p>
            </li>
            <li>
              <h4>The Load Tests Button</h4>
              <p>
                This button returns the names of each <code>.test</code> file 
                contained in the directory you specified in the directory input.
                The returned <code>.test</code> files will be rendered as a list
                below the interactive components
              </p>
            </li>
            <li>
              <h4>The Run Tests Button</h4>
              <p>
                This button returns the names of each <code>.test</code> file 
                contained in the directory you specified in the directory input.
                The returned <code>.test</code> files will be rendered as a list
                below the interactive components
              </p>
            </li>
            <li>
              <h3>The Help Button</h3>
              <p>
                This button brings up this help menu.
              </p>
            </li>
          </ul>
          <h3>The Test List</h3>
          <p>
            The test list is where test files are rendered. When you click the
            load tests button, a list of <code>.test</code> files are rendered
            on the screen. This is a list of test files with the specified
            directory that can be run. For each test, you can see the name of the
            <code>.test</code> file as well as a question mark icon indicating
            that the test has not been run.
          </p>
          <p>
            If you click the run all tests button, all the tests will run and
            the results of each test will be visible to the user. Like with the
            load tests button, the names of each <code>.test</code> file are
            rendered to the screen. But now, there will be a checkmark icon or an
            X icon next to each file name. A checkmark means that all of the tests
            in the given <code>.test</code> file passed. An X means that at least
            one test failed in the given <code>.test</code> file.
          </p>
          <h3>Test-Specific Information</h3>
          <p>
            After you run tests with the run all tests button, you are able to click
            on the name of the <code>.test</code> file. A pop-up will appear with
            the name of each test in the <code>.test</code> file as well as a
            line-by-line comparison of the expected output and the actual output,
            for each test.
          </p>
        </div>
        <button onClick={handleClose}>Close</button> 
      </div>
    </div>
  )
}

export default HelpMenu