import { Icon } from "@iconify/react"
import { TestFile } from "../types"

interface TestFileProps {
  testFile: TestFile
  showMoreInfo: (test: TestFile) => void
}

const TestFileComponent = ({testFile, showMoreInfo}: TestFileProps) => {
  return (
    <li className="test-file" onClick={() => testFile.result !== "" && showMoreInfo(testFile)}>
      <div className="test-file-heading">
        <span className="test-file-title">{testFile.filePath}</span>
        {" " /* adding a space between the test name and test result */}
        {testFile.result !== "" && <span className="test-file-result">[{testFile.result}]</span>}
      </div>
      <Icon
        data-test-result={testFile.result}
        className="test-result-icon"
        icon={(() => {
          switch (testFile.result) {
            case "passed": {
              return "bi:check-circle-fill"
            }
            case "failed": {
              return "bi:x-circle-fill"
            }
            default: {
              return "bi:question-circle-fill"
            }
          }
        })()}/>
    </li>
  )
}

export default TestFileComponent