import { useState } from 'react';
import AccessibilityBar from './components/accessibility-bar/AccessibilityBar';
import TesterInstance from './components/TesterInstance';
import "./styles/tester-styles/tester.css"
import "./styles/tester-styles/testModal.css"
import "./styles/tester-styles/helpMenu.css"
import "./styles/accessibility-styles/colorPalette.css"
import "./styles/screenreader-styles/aboutModal.css"
import "./styles/screenreader-styles/accessibilityBar.css"
import "./styles/screenreader-styles/awesomeButtonStyle.css"
import {TestFile} from "./types";

function App() {
  const [colorPalette, setColorPalette] = useState<string>("normalDark");
  const [testModalIsOpen, setTestModalIsOpen] = useState<boolean>(false);
  const [testFiles, setTestFiles] = useState<Array<TestFile>>([]);
    return (
    <div className="App">
      <AccessibilityBar 
        colorPalette={colorPalette}
        setColorPalette={setColorPalette}
        testModalIsOpen={testModalIsOpen}
        testFiles={testFiles}/>
      <TesterInstance 
        colorPalette={colorPalette}
        testModalIsOpen={testModalIsOpen}
        setTestModalIsOpen={setTestModalIsOpen}
        testFiles={testFiles}
        setTestFiles={setTestFiles}/>
    </div>
  );
}

export default App;
