import React, {useEffect, useState} from "react"
import { GetTestsResponse, RunTestsResponseList, Test, TestFile } from "../types"
import HelpMenu from "./HelpMenu"
import TestModal from "./test-modal/TestModal"
import TestFileComponent from "./TestFileComponent"


interface TesterInstanceProps {
  testModalIsOpen: boolean
  setTestModalIsOpen: React.Dispatch<React.SetStateAction<boolean>>
  colorPalette: string
  testFiles: Array<TestFile>
  setTestFiles: React.Dispatch<React.SetStateAction<Array<TestFile>>>
}

function formatRanTests(response: RunTestsResponseList, directoryPrefix: string): Array<TestFile> {
  const testFilesMap = new Map<string, Array<Test>>()
  for (const result of response.test_results) {
    const newTest: Test = {
      testName: result.test_name,
      diffs: result.diffs,
      result: result.diffs.reduce((acc, cur) => acc && cur.type === "UNCHANGED", true) ? "passed" : "failed"
    }
    if (testFilesMap.has(result.test_dir)) {
      testFilesMap.get(result.test_dir)!.push(newTest)
    } else {
      testFilesMap.set(result.test_dir, [newTest])
    }
  }
  const testFiles: Array<TestFile> = []
  testFilesMap.forEach((tests, testDir) => {
    const testFile: TestFile = {
      filePath: testDir.replace(directoryPrefix, ""),
      result: tests.reduce((acc, cur) => acc && cur.result === "passed", true) ? "passed" : "failed",
      tests: tests
    }
    testFiles.push(testFile)
  })
  return testFiles
}

function formatDirectoryPrefix(directory: string): string {
  let directoryPrefix = directory
  if (directoryPrefix.includes("/")) {
    directoryPrefix = directory.replaceAll("/", "\\")
  }
  if (!directoryPrefix.endsWith("\\")) {
    directoryPrefix += "\\"
  }
  return directoryPrefix
}

const TesterInstance = ({ testModalIsOpen, setTestModalIsOpen, colorPalette, testFiles, setTestFiles}: TesterInstanceProps) => {
  // const [testFiles, setTestFiles] = useState<Array<TestFile>>([])
  const [showHelpMenu, setShowHelpMenu] = useState<boolean>(false)
  const [modalTest, setModalTest] = useState<TestFile | null>(null)
  const [directory, setDirectory] = useState<string>("")

  const getTests = async (directory: string) => {
    const rawResponse = await fetch("http://localhost:3200/get_tests", {
      headers: {
        'Content-Type': 'application/json'
      },
      method: "POST",
      body: JSON.stringify({dir: directory})
    })
    const getTestsResponse: GetTestsResponse = await rawResponse.json()
    const receivedTestFiles: Array<TestFile> = []
    const directoryPrefix = formatDirectoryPrefix(directory)
    for (const testName of getTestsResponse.test_dirs) {
      const newTest: TestFile = {
        filePath: testName.replace(directoryPrefix, ""),
        result: "",
        tests: []
      }
      receivedTestFiles.push(newTest)
    }
    setTestFiles(receivedTestFiles)
  }

  const runTests = async (directory: string) => {
    const rawResponse = await fetch("http://localhost:3200/run_tests", {
      headers: {
        'Content-Type': 'application/json'
      },
      method: "POST",
      body: JSON.stringify({dir: directory})
    })
    const runTestsResponse: RunTestsResponseList = await rawResponse.json()
    const directoryPrefix = formatDirectoryPrefix(directory)
    const testFiles = formatRanTests(runTestsResponse, directoryPrefix)
    setTestFiles(testFiles)
  }

  const showMoreInfo = (testDatum: TestFile) => {
      setModalTest(testDatum)
      setTestModalIsOpen(true)
  }

  return (
    <main id="tester-instance" className={colorPalette}>
      {modalTest !== null && <TestModal showModal={testModalIsOpen} testFile={modalTest} handleClose={() => setTestModalIsOpen(false)} />}
      <HelpMenu showMenu={showHelpMenu} handleClose={() => setShowHelpMenu(false)} />
      <div id="top-section" className="top-section">
        <h1 id="title">CS32 System Tester</h1>
        <form id="directory-form" onSubmit={(e) => {
          e.preventDefault()
          getTests(directory)
        }}>
          <input
            id="directory-input"
            type="text"
            placeholder="Type directory here..."
            value={directory}
            autoComplete="off"
            onChange={(e) => setDirectory(e.target.value)}/>
          <input 
            id="directory-submit"
            className="tester-action-button"
            type="submit"
            value="Load Tests"/>
          <button 
            id="run-tests-button"
            className="tester-action-button"
            onClick={(e) => {
              e.preventDefault()
              runTests(directory)
            }}>
            Run All Tests
          </button>
          <button 
            id="help-button"
            className="tester-action-button"
            onClick={(e) => {
              e.preventDefault()
              setShowHelpMenu(true)
            }}>
            Help
          </button>
        </form>
      </div>
      <ol id="test-file-list" className="test-list">
        {testFiles.map((t, index) => 
            <TestFileComponent
              testFile={t}
              showMoreInfo={showMoreInfo}
              key={index}/>)}
      </ol>
    </main>
  )
}

export default TesterInstance