import React, {useEffect, useState} from "react";
//@ts-ignore
import {AwesomeButton} from 'react-awesome-button';
import ReactHotkeys from "react-hot-keys";
import {TestFile} from "../../../types";

// Look up the Web Speech API (https://developer.mozilla.org/en-US/docs/Web/API/SpeechSynthesis)
let VOICE_SYNTH: SpeechSynthesis = window.speechSynthesis;

// custom element for ELEMENT_HANDLERS
type element = {
    htmlElement : Element
    index : number;
    func : Function
    isInteractive : boolean;
}
// Stores elements and their handler functions
let ELEMENT_HANDLERS : Array<element> = [];

// Indicates the current element that the user is on
let current : element;

// Indicates the currentUtterance being worked on.
// allows forced wait when cancelled.
let currentUtterance : SpeechSynthesisUtterance | null;

interface ScreenReaderButtonsProps {
    aboutModalOpen: boolean
    testModalOpen: boolean
    testFiles: Array<TestFile>
}

function sleep(ms : number) {
    return new Promise(resolve => setTimeout(resolve, ms));
}
const wait = async () => {
    const data = await sleep(250);
}

function ScreenReaderButtons({ aboutModalOpen, testModalOpen, testFiles } : ScreenReaderButtonsProps) {

    useEffect(() => {
        wait().then(r => loadPageData());
        }, [aboutModalOpen, testModalOpen, testFiles]
    )

    const globalHotkeys : string = "left, right, up, down, p, r"
    const disableHotkeys : string = ""

    const[speechSpeed, setSpeechSpeed] = useState(1.0);
    const[pauseStatus, setPauseStatus] = useState("Start");
    const[curHotKeys, setCurHotKeys] = useState(globalHotkeys);

    const speedChange = (addition : number) => {
        if(speechSpeed + addition > 0){
            setSpeechSpeed(speechSpeed + addition);
        }
    }
    const setPlayOrPaused = () => {
        switch (pauseStatus){
            case "Start" :
            if(ELEMENT_HANDLERS.length === 0){
                currentUtterance = new SpeechSynthesisUtterance(
                    "Data not loaded. Please load first.");
                currentUtterance.rate = speechSpeed;
                VOICE_SYNTH.speak(currentUtterance);
                break;
            }
            setPauseStatus("Playing");
            nextPrevStartHandler("start");
            break;
            case "Paused" : setPauseStatus("Playing");
            VOICE_SYNTH.resume();
            break;
            case "Playing" : setPauseStatus("Paused");
            VOICE_SYNTH.pause();
            break;
        }
    }
    /**
     *  Generates all handlers for relevant elements.
     */
    function generateHandlers(): void {
        if(testModalOpen){
            let testModalTitle : HTMLCollection = document.getElementsByClassName("test-modal-title");
            let testModalTests : HTMLCollection = document.getElementsByClassName("test-list");
            let testModalButton : HTMLCollection = document.getElementsByClassName("close-modal-button");

            let testModalTestElements : HTMLCollection = testModalTests[0].getElementsByClassName("test");


            tagFunctionality(testModalTitle);
            tagFunctionality(testModalTestElements);
            tagFunctionality(testModalButton);

        } else if (aboutModalOpen) {
            let aboutModal : HTMLCollection = document.getElementsByClassName("ReactModal__Content");
            let aboutModalElements : HTMLCollection = aboutModal[0].getElementsByTagName('*');
            tagFunctionality(aboutModalElements);
        } else {
            //only looking at test results and specific areas to read.
            let topSection : HTMLCollection = document.getElementsByClassName("top-section");
            let testList : HTMLCollection = document.getElementsByClassName("test-list");

            let topSectionElements : HTMLCollection = topSection[0].getElementsByTagName("*");
            let testListElements : HTMLCollection = testList[0].getElementsByTagName("*");

            tagFunctionality(topSectionElements);
            tagFunctionality(testListElements);
        }
    }
    /**
     * generates and adds elements with respective handlers to ELEMENT_HANDLERS
     * @param items collection of HTML elements.
     * @param index current index of item being looked at.
     */
    function tagFunctionality(items : HTMLCollection): void {

        for (let i = 0; i < items.length; i++) {

            let curElement: Element = items.item(i) as Element;
            if (curElement === null) {
                console.log("Error, tagging problem.");
                return;
            }
            let tag: string = curElement.tagName;
            let tempElement: element
            switch (tag) {
                case 'H1' :
                case 'H2' :
                case 'H3' :
                case 'H4' :
                case 'H5' :
                case 'H6' :
                case 'P' :
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak("" + curElement.textContent),
                        isInteractive: false
                    }
                    break;
                case 'svg' :
                    let outputText : string = ""
                    //@ts-ignore
                    if(curElement.dataset.testResult.length === 0){
                        outputText = "An icon representing an un-run test."
                    } else {
                        //@ts-ignore
                        outputText = "An icon representing: " + curElement.dataset.testResult
                    }
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak(outputText),
                        isInteractive: false
                    }
                    break;
                case 'FORM' :
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak("New form."),
                        isInteractive: false
                    }
                    break;
                case 'INPUT' :
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak("Input with type: " + curElement.type
                            + ". press enter to interact, escape to continue."),
                        isInteractive: true
                    }
                    break;
                case 'BUTTON' :
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak("Button of type: " + curElement.type
                            //@ts-ignore since we already checked null before
                            + ", with text: " + curElement.innerHTML
                            + ". press enter to interact, escape to continue."),
                        isInteractive: true
                    }
                    break;
                case 'LI' :
                    tempElement = {
                        htmlElement: curElement,
                        index: ELEMENT_HANDLERS.length,
                        //@ts-ignore since we already checked null before
                        func: () => speak(curElement.innerHTML),
                        isInteractive: false
                    }
                    break;
                default :
                    break;

            }
            // @ts-ignore
            if(tempElement != null){
                ELEMENT_HANDLERS.push(tempElement);
            }
        }
    }

    /**
     * Handles next, previous, and start,
     * based on whether or not stuff is currently being spoken.
     * @param type string type of thing to be handled.
     */
    async function nextPrevStartHandler(type : string) {
        //checks if currently speaking, if so cancels and waits for end.
        if(VOICE_SYNTH.speaking){
            VOICE_SYNTH.cancel()
            if (currentUtterance){
                await new Promise<void>((resolve) => {
                    //@ts-ignore already checked for null in if statement
                    currentUtterance.onend = () => {resolve()};
                    currentUtterance = null;
                })
            }
            switch(type) {
                case 'next' : {
                    if(current.index + 1 < ELEMENT_HANDLERS.length){
                        //starts next utterance
                        current = ELEMENT_HANDLERS[current.index + 1];
                        current.func();
                    }
                    break;
                }
                case 'prev' : {
                    if(current.index - 1 >= 0){
                        //starts previous utterance
                        current = ELEMENT_HANDLERS[current.index - 1];
                        current.func();
                    }
                    break;
                }
                case 'start' : {
                    current = ELEMENT_HANDLERS[0];
                    current.func();
                    break;
                }
            }
        } else {
            //occurs if not speaking, will start at start for next and start
            //start at last element if prev pressed.
            switch(type) {
                case 'next' : {
                    current = ELEMENT_HANDLERS[0];
                    current.func();
                    break;
                }
                case 'prev' : {
                    current = ELEMENT_HANDLERS[ELEMENT_HANDLERS.length-1];
                    current.func();
                    break;
                }
                case 'start' : {
                    current = ELEMENT_HANDLERS[0];
                    current.func();
                    break;
                }
            }
        }
    }

    /**
     * Generates and executes a speech request.
     * @param text A string, the text being spoken.
     */
    function speak(text: string): void {
        if (VOICE_SYNTH) {
            // initialize a speech request using SpeechSynthesisUtterance
            // https://developer.mozilla.org/en-US/docs/Web/API/SpeechSynthesisUtterance
            currentUtterance = new SpeechSynthesisUtterance(text);
            currentUtterance.rate = speechSpeed;
            let curIndex : number = current.index;
            currentUtterance.onstart = async function () {
                await new Promise<SpeechSynthesisUtterance>((resolve) => {
                    //@ts-ignore already set to new utterance
                    currentUtterance.onend = async () => {
                        if(current.isInteractive){
                            await interactiveElementHandler(current);
                        }
                        //@ts-ignore already set to new utterance
                        resolve(currentUtterance);
                    }
                })
                if(curIndex + 1 < ELEMENT_HANDLERS.length){
                    current = ELEMENT_HANDLERS[curIndex + 1];
                    ELEMENT_HANDLERS[current.index].func();
                } else {
                    //sets speaking to false once done and no more in queue.
                    currentUtterance = null;
                }
            }
            VOICE_SYNTH.speak(currentUtterance);
        }
    }

    const loadPageData = async () => {
        if (VOICE_SYNTH.speaking) {
            VOICE_SYNTH.cancel()
            if (currentUtterance) {
                await new Promise<void>((resolve) => {
                    //@ts-ignore already checked for null in if statement
                    currentUtterance.onend = () => {
                        resolve()
                    };
                    currentUtterance = null;
                })
            }
        }
        ELEMENT_HANDLERS = [];
        generateHandlers();
        setPauseStatus("Start");
    }

    const keyHandler = (keyname : any, e : any, handle : any) => {
        e.preventDefault();
        switch (keyname) {
            case "up" : speedChange(0.25);
            break;
            case "down" : speedChange(-0.25);
            break;
            case "right" : nextPrevStartHandler("next");
            break;
            case "left" : nextPrevStartHandler("prev");
            break;
            case "p" : setPlayOrPaused();
            break;
            case "r" : loadPageData();
            break;
            default:
                break;
        }
    }

    /**
     * interactive element handler, with the assistance of elementEscapeHandler,
     * deal with handling interactive events, allowing the user to type in text,
     * enter links, and more.
     * @param element the element currently being handled.
     * @returns a promise, noting finished interaction.
     */
    async function interactiveElementHandler (element : element) : Promise<void>{
        return new Promise((resolve) => {
            //removes main listener to allow full interaction with on screen elements. Escape
            //will return control to screenreader on selected hotkeys.
            setCurHotKeys(disableHotkeys);
            document.addEventListener('keydown', pauseInputHandler);
            async function pauseInputHandler(e: KeyboardEvent) {
                if (e.key === "Enter") {
                    e.preventDefault()
                    document.removeEventListener('keydown', pauseInputHandler);
                    let curElement : HTMLElement = document.getElementById(element.htmlElement.id) as HTMLElement;
                    if(curElement.tagName === "A"){
                        //@ts-ignore curElement.href does exist, we know since this is an a tag.
                        window.open(curElement.href);
                    } else {
                        //i.e., not a link.
                        curElement.focus();
                    }
                    //waits for handler to return promise
                    await elementEscapeHandler();
                    curElement.blur();
                    resolve();
                } else if (e.key === "Escape") {
                    e.preventDefault()
                    document.removeEventListener('keydown', pauseInputHandler);
                    //relinquishes control back to main listener.
                    setCurHotKeys(globalHotkeys);
                    resolve();
                }
            }
        });
    }

    /**
     * Watches for use of the escape key, eventually used to continue the screenreader.
     * Allows for use of other keys.
     * @returns Promise
     */
    function elementEscapeHandler () : Promise<void>{
        return new Promise((resolve) => {
            document.addEventListener('keydown', escapeWatcher);
            function escapeWatcher(e: KeyboardEvent) {
                //at this point, already interacting with element, only need escape.
                if (e.key === "Escape") {
                    e.preventDefault();
                    document.removeEventListener('keydown', escapeWatcher);
                    setCurHotKeys(globalHotkeys);
                    resolve();
                }
            }
        });
    }

    return(
            <div id="screenreaderArea">
                <h3 id="areaText">Screen Reader Control:</h3>
                <ReactHotkeys keyName={curHotKeys}
                              onKeyDown={(keyName, e, handle)=>keyHandler(keyName, e, handle)}/>
                <AwesomeButton type='primary' onPress={()=>setPlayOrPaused()}>{pauseStatus}</AwesomeButton>
                <AwesomeButton type='primary' onPress={()=>nextPrevStartHandler("next")}>Next</AwesomeButton>
                <AwesomeButton type='primary' onPress={()=>nextPrevStartHandler("prev")}>Previous</AwesomeButton>
                <AwesomeButton type='primary' onPress={()=>speedChange(0.25)}>Speed+</AwesomeButton>
                <AwesomeButton type='primary' onPress={()=>speedChange(-0.25)}>Speed-</AwesomeButton>
                <AwesomeButton type='primary' onPress={()=>loadPageData()}>Reset</AwesomeButton>
                <h3 id='areaText'>Speed: {speechSpeed}</h3>
            </div>
    )

}
export default ScreenReaderButtons;