import { Test } from "../../types"
import DiffsList from "./DiffsList"

interface TestComponentProps {
  test: Test
}

const TestComponent = ({ test }: TestComponentProps) => {
  return (
    <li className="test">
      <h3 className="test-name">{test.testName}</h3>
      <DiffsList diffs={test.diffs}/>
    </li>
  )
}

export default TestComponent