import { Diff } from "../../types"
import DiffComponent from "./DiffComponent"

interface DiffsProps {
  diffs: Array<Diff>
}

const DiffsList = ({diffs}: DiffsProps) => {
  return (
    <ol className="diffs-list">
      <div className="diffs-list-label">
        <h4 className="label-expected">Expected Output</h4>
        <h4 className="label-actual">Actual Output</h4>
      </div>
      {diffs.map((d, index) => <DiffComponent key={index} lineNumber={index + 1} diff={d} />)}
    </ol>
  )
}

export default DiffsList