import { Diff } from "../../types"

interface DiffComponentProps {
  lineNumber: number
  diff: Diff
}

const DiffComponent = ({lineNumber, diff}: DiffComponentProps) => {
  return (
    <li className="diff" data-diff-type={diff.type}>
      <div className="diff-col">
        <div className="diff-line-number">{lineNumber}</div>
        <div>{diff.expected}</div>
      </div>
      <div className="diff-col">
        <div className="diff-line-number">{lineNumber}</div>
        <div>{diff.actual}</div>
      </div>
    </li>
  )
}

export default DiffComponent