// @ts-ignore
import Modal from "react-modal"
// @ts-ignore

function AboutModal (props : any) {

    return(
        <div id={props.colorPalate}>
            <Modal
                isOpen={props.aboutModalOpen}
                onRequestClose={props.closeModal}
                contentLabel="Example Modal"
                ariaHideApp={false}
                // style={customStyles}
                className="AboutModal"
                overlayClassName="AboutOverlay">
                {/*<ScreenreaderButtons/>*/}
                <div>
                    <h2>About</h2>
                    <p>Welcome to the CS32 System tester! To see how to use the tester,
                        view the help menu next to the searchbar. <br/> Want to use the accessibility options?
                        Keep reading below.</p>
                    <h2>Information: Screen Reader</h2>
                    <p>There are two ways to control the screen reader: through the buttons at the top of the screen,
                    or through hotkeys that we've set up. <br/>
                        Here are the keyboard controls:</p>
                    <ol>
                        <li>'r' tells screen reader to re-categorize information from the page.
                        </li>
                        <li>
                            'p' starts/pauses/resumes the screenreader, depending on what state the reader is currently in.
                        </li>
                        <li>
                            'left/right' arrow keys correspond to moving the the previous and next element in the reader,
                            respectively.
                        </li>
                        <li>
                            'up/down' arrow keys increase or decrease screen reader speed, respectively.
                        </li>
                    </ol>
                    Notes:
                    <br/>
                    <p>
                    Any changes in the webpage, opening/closing modals, and running/loading new tests will refresh the screenreader with new information, and thus will cause the <br/> screenreader to stop and reset.
                    <b> If screenreader issues occur, pressing the reset button or 'r' will reset the screenreader to the start and refresh information.</b>
                    </p>
                    <h2>Information: Color Adjustment</h2>
                    <p>In the pursuit of accessibility, we've created a few color palates
                    to help colorblind/weak users help differentiate between crucial issue between inputs and outputs.
                    <br/> Each mode has a light and dark mode, so whether you're testing in the darkness of night, or
                    out on the main green in broad daylight, you'll be able to see your results.</p>
                    <ol>
                        <li>Normal mode: for people not experiencing color weakness or blindness.</li>
                        <li>Partial Colorblind Friendly: for people experiencing color weakness/blindness in certain colors.</li>
                        <li>Grayscale Mode: For people who are fully colorblind, increases contrast so things are more noticeable on screen.</li>
                    </ol>

                    <button type={undefined} onClick={props.closeModal}>Close</button>
                </div>
            </Modal>
        </div>
    )
}
export default AboutModal;